* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.button__start, .button__stop {
  width: 10%;
  height: 10%;
  border: 1px double #00f;
  border-radius: 10%;
  font-size: 300%;
  position: absolute;
}

.button__stop {
  top: 50%;
  left: 50%;
}

.button__start {
  top: 50%;
  left: 37%;
}

.button__start:active, .button__stop:active {
  transform: scale(.9);
}

